* {
  font-family: "Open Sans", serif;
}

body {
  display: flex;
  align-items: center;
  height: 100vh;
}

h1,
h2 {
  font-family: "Abril Fatface", serif;
  margin: 0;
  padding: 0;
}

.wrapper {
  border: 1px solid black;
  max-width: 980px;
  margin: 0 auto;
  box-shadow: 7px 4px 0px black;
  flex-basis: 100%;
  padding: 2rem;
}

.title {
  font-size: 48px;
  text-align: center;
  margin-top: 2rem;
}

.subtitle {
  font-size: 32px;
  text-align: center;
}

.new-question {
  text-align: center;
  cursor: pointer;
}
.new-question:hover {
  text-decoration: underline;
}

.question {
  text-align: center;
  font-size: 30px;
  font-family: "Open Sans";
  margin-top: 4rem;
}

.selected-theatre {
  text-decoration: underline;
  font-family: "Abril Fatface", serif;
}

.answers-wrapper {
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

button {
  text-transform: uppercase;
  font-family: "Open Sans";
  font-size: 18px;
  border-radius: 0;
  box-shadow: none;
  background: white;
  border: 2px solid black;
  cursor: pointer;
  padding: 0.8rem 1.25rem;
  font-weight: 600;
}

.result-text {
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 32px;
  font-weight: bold;
  min-height: 44px;
}

.mission {
  max-width: 700px;
  margin: 0 auto;
  font-family: "Open Sans";
  font-size: 16px;
  border: 2px dashed black;
  padding: 2rem 1rem;
  text-align: center;
  display: none;
}
.mission.show {
  display: block;
}
.mission span {
  display: block;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 4px;
}

footer {
  font-size: 0.75rem;
  text-align: center;
  margin-top: 0.5rem;
}